/* eslint-disable max-len */
import { NgClass } from '@angular/common';
import { Component, Input, OnInit } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { Invoice } from '@biz-nest/commerce-client';
import { FrontendLogger, IfilterProps } from '@biz-nest/core-client';
import {
  CurrencyService, openClose, TooltipService
} from '@front-shared-lib';

// eslint-disable-next-line @typescript-eslint/naming-convention
declare let ApexCharts;

@Component({
  selector: 'app-one-company-sales-analytics-ecommerce',
  standalone: true,
  imports: [
    FormsModule,
    ReactiveFormsModule,
    RouterModule,
    RouterModule,
    NgClass
  ],
  animations: [
    openClose
  ],
  templateUrl: './one-company-sales-analytics-ecommerce.component.html'
})
export class OneCompanySalesAnalyticsEcommerceComponent
implements OnInit {
  @Input({ required: true }) companyId!: string;
  thisMonthNumber = new Date().getMonth();
  currentActivePeriod: 'lastMonth' | 'thisMonth' | 'thisYear' = 'thisYear';

  constructor(
    public tooltipService: TooltipService,
    public currencyService: CurrencyService
  ) {}

  ngOnInit(): void {
    this.getYearlyEcomerceSalesAnalytics();
  }

  getEcomerceSalesAnalyticsSolo(startDate: Date, endDate: Date) {
    const filter: IfilterProps = {
      propFilter: {
        company: this.companyId
      },
      comparisonFilter: [
        {
          field: 'createdAt',
          fieldValue: startDate,
          operator: 'gte'
        },
        {
          field: 'createdAt',
          fieldValue: endDate,
          operator: 'lte'
        }
      ]
    };

    return Invoice.getEcommerceSalesAnalytics(filter);
  }

  async getMonthlyEcomerceSalesAnalytics(month: number) {
    const daysInMonth = Array
      .from(Array(new Date(new Date().getFullYear(), month + 1, 0).getDate()).keys()).map(x => x + 1);

    const promises = daysInMonth.map(day => this.getEcomerceSalesAnalyticsSolo(
      new Date(new Date().getFullYear(), month, day),
      new Date(new Date().getFullYear(), month, day + 1)
    ));

    const all = await Promise.all(promises);

    const totalAmountData = all.map(val => val.totalSales);
    const totalTaxData = []; // TODO
    const countData = all.map(val => val.count);
    const days = daysInMonth.map(day => day.toString());

    this.currentActivePeriod = this.thisMonthNumber === month ? 'thisMonth' : 'lastMonth';
    this.renderEcomerceChat(totalAmountData, totalTaxData, countData, days);
  }

  async getYearlyEcomerceSalesAnalytics() {
    const months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];

    const promises = months.map((month, index) => this.getEcomerceSalesAnalyticsSolo(
      new Date(new Date().getFullYear(), index, 1),
      new Date(new Date().getFullYear(), index + 1, 1)
    ));

    const all = await Promise.all(promises);


    const totalAmountData = all.map(val => val.totalSales);
    const totalTaxData = []; // TODO
    const countData = all.map(val => val.count);

    this.currentActivePeriod = 'thisYear';
    this.renderEcomerceChat(totalAmountData, totalTaxData, countData, months);
  }

  renderEcomerceChat(
    salesData: number[],
    taxData: number[],
    countData: number[],
    categories: string[]
  ) {
    FrontendLogger.debug('renderEcomerceChat', salesData, taxData, countData, categories);
    const options = {
      series: [{
        name: 'Sales',
        data: salesData
      },
      {
        name: 'Tax',
        data: taxData
      },
      {
        name: 'Count',
        data: countData
      }
      ],
      chart: {
        type: 'bar',
        height: 400,
        stacked: true,
        toolbar: {
          show: false
        }
      },
      plotOptions: {
        bar: {
          columnWidth: '20%'
        }
      },
      colors: ['#4CAF50', '#2196F3', '#FF9800'],
      fill: {
        opacity: 1
      },
      dataLabels: {
        enabled: false
      },
      legend: {
        show: false
      },
      yaxis: {
        labels: {
          formatter: function(y) {
            return y.toFixed(0) + '%';
          }
        }
      },
      xaxis: {
        categories,
        labels: {
          rotate: -90
        }
      }
    };

    const chart = new ApexCharts(document.querySelector('#e-sale-analytics'), options);

    chart.render();
  }
}
