import { DatePipe } from '@angular/common';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { ActivatedRoute, Router, RouterModule } from '@angular/router';
import { Company } from '@biz-nest/commerce-client';
import { FrontendLogger, IcommerceUser, IfileMeta } from '@biz-nest/core-client';
import {
  AppNameService, EventbusService, MainBarTitleService,
  openClose,
  ViewOneCompose
} from '@front-shared-lib';
import { Icrumb } from '@main-shared-lib';
import { AdminBackBarComponent } from '../../../misc/admin-back-bar/admin-back-bar.component';
import { AdminPageContentComponent } from '../../../misc/admin-page-content/admin-page-content.component';
import {
  SubscriptionListSharableComponent
} from '../../../subscriptions/subscription-list-sharable/subscription-list-sharable.component';
import { OneCompanyCardsComponent } from '../one-company-cards/one-company-cards.component';
import {
  OneCompanySalesAnalyticsEcommerceComponent
} from '../one-company-sales-analytics-ecommerce/one-company-sales-analytics-ecommerce.component';

@Component({
  selector: 'app-view-company',
  standalone: true,
  imports: [
    RouterModule,
    DatePipe,
    OneCompanyCardsComponent,
    OneCompanySalesAnalyticsEcommerceComponent,
    AdminBackBarComponent,
    AdminPageContentComponent,
    SubscriptionListSharableComponent
  ],
  animations: [
    openClose
  ],
  templateUrl: './view-company.component.html',
  styleUrl: './view-company.component.scss'
})
export class ViewCompanyComponent
implements OnInit, OnDestroy {
  viewOneCompose = new ViewOneCompose(
    Company,
    { route: ['/company/update'], eventName: 'updateCompany' },
    this.router,
    this.route,
    this.eventbus
  );

  title = 'View';
  breadCrumbs: Icrumb[] = [
    {
      name: 'Dashboard',
      icon: 'space_dashboard',
      link: '/'
    }, {
      name: 'Company',
      icon: 'account_balance',
      link: '/company/list'
    }, {
      name: 'View Company',
      icon: 'visibility'
    }
  ];


  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private eventbus: EventbusService,
    private apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    private metaTagService: Meta,
    private titleService: Title
  ) { }

  get photos(): IfileMeta[] {
    return typeof this.viewOneCompose
      .oneItem?.owner === 'string' ? [] : this.viewOneCompose.oneItem?.owner.photos || [];
  }

  async ngOnInit() {
    try {
      await this.viewOneCompose.init();

      FrontendLogger.debug('ONE ITEM ', this.viewOneCompose.oneItem);

      this.title = this.viewOneCompose.oneItem?.displayName || 'View';

      this.mainBarTitle.title = 'View Company - ' + this.title;

      this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
      this.metaTagService.updateTag({
        name: 'description',
        content: `View and manage the details of
        a company to ensure accurate
        information and effective business operations.`
      });
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  getCompanyOwner() {
    return this.viewOneCompose.oneItem?.owner as IcommerceUser;
  }

  subscribeToPkg(id: string) {
    this.router.navigate(['subscribe/' + id]);
  }

  ngOnDestroy() {
    this.viewOneCompose.cleanup?.();
  }
}

