import { NgClass } from '@angular/common';
import { Component, Input, OnInit } from '@angular/core';
import { Router, RouterModule } from '@angular/router';
import { EcommerceOrder } from '@biz-nest/commerce-client';
import { FrontendLogger } from '@biz-nest/core-client';
import { openClose } from '@front-shared-lib';
import {
  TecomSalePeriod, dashCardsBmUse,
  salesAnalyticsEcommerceForSharedUse
} from '@main-shared-lib';


@Component({
  selector: 'app-one-company-cards',
  standalone: true,
  imports: [
    RouterModule,
    NgClass
  ],
  animations: [
    openClose
  ],
  templateUrl: './one-company-cards.component.html',
  styleUrl: './one-company-cards.component.scss'
})
export class OneCompanyCardsComponent
implements OnInit {
  @Input({ required: true }) companyId!: string;
  customerCount = 0;
  customerIncreDecreStyle?: string;
  customerIncreDecre = 0;
  invoiceIncreDecreStyle?: string;
  invoiceIncreDecre = 0;
  countInvoices = 0;
  estimateCount = 0;
  salesData = {
    totalSales: 0,
    tax: 0,
    count: 0,
    earnings: 0
  };

  salesPercentage = 0;
  revenuePercentage = 0;
  pendingOrders = 0;

  constructor(private router: Router) {}

  ngOnInit() {
    this.getEcomSalesTotal();
    this.fetchData();
  }

  async getEcomSalesTotal(period?: TecomSalePeriod) {
    try {
      const { getAllSales } = salesAnalyticsEcommerceForSharedUse();

      const {
        salesData,
        salesPercentage,
        revenuePercentage
      } = await getAllSales(period);

      this. salesData = salesData;
      this.salesPercentage = salesPercentage;
      this.revenuePercentage = revenuePercentage;
    } catch (error) {
      FrontendLogger.error('Error fetching', error);
    }
  }

  async fetchData() {
    const {
      getCustomers,
      getAllInvoicesTotal,
      getEstimatesCount,
      monthlyEvaluationCustomers,
      monthlyEvaluationInvoices
    } = dashCardsBmUse(this.companyId);

    try {
      const [
        customerCount,
        countInvoices,
        estimateCount,
        {
          customerIncreDecre,
          customerIncreDecreStyle
        },
        {
          invoiceIncreDecre,
          invoiceIncreDecreStyle
        }
      ] = await Promise.all([
        getCustomers(),
        getAllInvoicesTotal(),
        getEstimatesCount(),
        monthlyEvaluationCustomers(),
        monthlyEvaluationInvoices()
      ]);


      this.customerCount = customerCount;
      this.countInvoices = countInvoices;
      this.estimateCount = estimateCount;
      this.customerIncreDecre = customerIncreDecre;
      this.customerIncreDecreStyle = customerIncreDecreStyle;
      this.invoiceIncreDecre = invoiceIncreDecre;
      this.invoiceIncreDecreStyle = invoiceIncreDecreStyle;
    } catch (error) {
      FrontendLogger.error('Error fetching data:', error);
    }
  }

  async getCompanyOrdersCount() {
    try {
      const { count } = await EcommerceOrder
        .filterAll({
          propFilter: {
            company: this.companyId,
            status: 'pending'
          },
          returnEmptyArray: true
        });

      this.pendingOrders = count;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  navigate(route: string) {
    this.router.navigate([route]);
  }
}

